
import { defineComponent } from "vue";
import { mapState, mapActions } from "vuex";

export default defineComponent({
  name: "Searcher",
  computed: {
    ...mapState(["labs"]),
  },
  methods: {
    ...mapActions(["searchingLab"]),
    searchLab() {
      if (this.inputValue !== "") {
        this.searchingLab(this.inputValue);
        this.$router.push("/searchlab");
      } else {
        this.$router.push("/");
      }
    },
  },
  data() {
    return {
      inputValue: "",
    };
  },
});
