
import { defineComponent } from "vue";
import { mapMutations, mapState } from "vuex";

export default defineComponent({
  name: "GuidedFiler",
  computed: {
    ...mapState(["finalService", "price", "service"]),
  },
  methods: {
    ...mapMutations(["updateFinalService", "updatePrice", "updateService"]),
  },
  data() {
    return {
      serviceChosen: [],
      price: 0,
      service: {
        filmType: null,
        palette: null,
        scan: null,
        print: null,
        rollBack: null,
        price: 0,
      },
    };
  },
});
